import React ,{useState, useEffect, useCallback} from 'react';
import CardBox from '../../components/Card/card'
import Button from '../../components/Button/Button'
import Title from '../../components/Title/Title'

import Styles from './DictionCard.module.css'

import useWords from '../../hooks/WorterHook/useWords'

// import TextToJs from '../../worter/worterToJs'

var synth = window.speechSynthesis;
var utterThis = new SpeechSynthesisUtterance();
synth.getVoices()

// console.log(synth.getVoices());

const DictationCard =()=>{

const [ransomWordState, editRansomWordState] = useState('Willkommen')

const [stateOfCard, EditstateOfCards] = useState(true)


useEffect(()=>{
let voices = synth.getVoices();
utterThis.lang = 'de-DE';

    let deVoice = voices.filter(el=>el.lang === 'de-DE')
    // console.log(deVoice);

utterThis.voice = deVoice[1] || deVoice[0];


},[])


 const chossenWordGen =  useWords(1)

    const textaction =()=> {
     let  newWord = chossenWordGen()
     editRansomWordState(newWord)
     console.log('Text Action');
     EditstateOfCards(true)
    }


 const speackNow = useCallback((speed)=>{
    utterThis.rate = speed
    utterThis.text = ransomWordState
    synth.speak(utterThis)
  

},[ransomWordState])

useEffect(() => {
    setTimeout(()=>{
        speackNow(1);
        // console.log('Card Effect Speak');
    },100)
   
}, [ransomWordState,speackNow])


const h1Stlye = {color : '#023047'}


// const showButton =  <Button cliked={()=>textaction()} title="Normal"/>
// console.log('Diction');
    return (
        <div>
   <CardBox>
            <h1 style={h1Stlye} >BUCHSTABIERE DAS</h1>
            <Button synth={synth.speaking} cliked={()=>speackNow(0.5)} title="Langsam" class='Read'/>
            <Button cliked={()=>speackNow(1)} title="Normal" class='Read'/>

            <Title word={ransomWordState} titleClass={stateOfCard}/>
            <Button cliked={()=>EditstateOfCards(!stateOfCard)} title={stateOfCard ? 'Anzeigen' : "Ausblenden" } class='Show'/>
            <Button cliked={()=>textaction()} title="Nächstes Wort" class='Next'/>
            <span>© Royi Yaakoby</span>
        </CardBox >
       
        </div>
     
    )
};


export default DictationCard;