import React from 'react';
import Styles from './Button.module.css'

const Button = (props) => {

let  styleClass = [Styles.Button] 

switch (props.class) {
    case 'Read':
        styleClass.push(Styles.Read)
        break;

        case 'Show':
            styleClass.push(Styles.Show)
            break;
            
            case 'Next':
                styleClass.push(Styles.Next)
                break;
        
   

    default:

        break;
}

styleClass = styleClass.join(' ')

// console.log('Button ' + props.title);
 

    return (
       
            <button onClick={props.cliked} className={styleClass}  >
        {props.title}
            </button>

    )
}

export default Button;
