import { useEffect, useState} from 'react';
import mytext from './WortersBasic.txt'

const useWords = ()=> { 

   
   let [wordList, setWordList ] = useState([]);


   useEffect(  ()=>{
    fetch(mytext)
  .then((r) => r.text())
  .then(text  => {
     let texttoworld=text.split(/\s+/)
     setWordList(texttoworld)
     
      //  console.log('hook list')
      }) 
  },[] )

  
const chossenWordGen = ()=>{
  let number =  Math.floor((Math.random())*(wordList.length))
  // console.log(number + " !!!" + wordList.length);
  // let genWord  = wordList[number]
  // console.log('hook gen: '+"number :" + number + " word : " + genWord); 
  return wordList[number]
}
  

    // console.log('herehook end');
    

    return (chossenWordGen)
    
   }

export default useWords; 