import React from 'react';
import Styles from './Title.module.css'

const Title = (props)=> {
// console.log(props.titleClass);

    let title= ''
    let titleClass = Styles.Hide
    if (props.titleClass){
        titleClass = Styles.Hide 
        title = "??????????"
    }
    if ( !props.titleClass){
        titleClass  = Styles.Show
        title =props.word
    }

    // console.log(titleClass);
    return ( 
        <div className = {titleClass} >
            <h1>{title}</h1>
        </div>
    )
}

export default Title;